<template>
  <div class="content">
    <el-form
      :inline="true"
      ref="searchForm"
      :model="searchFormData"
      :rules="searchFormRule"
      status-icon
      hide-required-asterisk
      class="search-form"
    >
      <el-form-item label="姓名" prop="username">
        <el-input
          v-model="searchFormData.username"
          placeholder="姓名"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitSearch" :loading="searching">
          查询
        </el-button>
        <el-button type="primary" @click="loadUsers">
          重置
        </el-button>
      </el-form-item>
    </el-form>
    <div class="users-table">
      <el-table :data="users" border>
        <el-table-column prop="id" label="用户ID"></el-table-column>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="role" label="角色"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">
              编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      @size-change="handlePageChange"
      @current-change="handlePageChange"
      :current-page.sync="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size.sync="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalPage"
      background
      class="users-pagination"
    >
    </el-pagination>
    <el-dialog title="修改信息" :visible.sync="dialogEdit">
      <el-form
        ref="editForm"
        :model="formData"
        :rules="formRule"
        status-icon
        hide-required-asterisk
        label-position="left"
        label-width="20%"
      >
        <el-input type="hidden" v-model="formData.id"></el-input>
        <el-form-item prop="username" label="姓名">
          <el-input
            type="text"
            v-model="formData.username"
            placeholder="姓名"
          />
        </el-form-item>
        <el-form-item prop="email" label="邮箱">
          <el-input type="text" v-model="formData.email" placeholder="邮箱" />
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input
            type="password"
            v-model="formData.password"
            placeholder="密码"
          />
        </el-form-item>
        <el-form-item prop="role" label="角色">
          <el-select v-model="formData.role" placeholder="请选择角色">
            <el-option label="学生" value="student"></el-option>
            <el-option label="老师" value="teacher"></el-option>
            <el-option label="管理员" value="admin"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEdit = false">
          取 消
        </el-button>
        <el-button type="primary" @click="submitEdit()" :loading="editing">
          确定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "back-users",
  data() {
    return {
      users: [],
      currentPage: 1,
      totalPage: 1,
      pageSize: 10,
      dialogEdit: false,
      editing: false,
      formData: {
        id: 0,
        username: "",
        email: "",
        password: "",
        role: "student"
      },
      formRule: {
        username: [
          {
            required: true,
            message: "请输入姓名",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "请输入邮箱",
            trigger: "blur"
          },
          {
            type: "email",
            message: "必须是邮箱",
            trigger: "blur"
          }
        ]
      },
      searching: false,
      searchFormData: {
        username: ""
      },
      searchFormRule: {
        username: [
          {
            required: true,
            message: "请输入姓名",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions("admin_users", [
      "getUsers",
      "deleteUser",
      "editUser",
      "searchUser"
    ]),
    handleDelete(index, user) {
      this.$confirm("确认删除？此操作不可逆！", "删除", {
        confirmButtonText: "删除",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteUser(user.id)
            .then(res => {
              this.$delete(
                this.users,
                this.users.findIndex(item => item.id === user.id)
              );
              this.$notify.success({
                title: "删除成功",
                message: "删除成功"
              });
            })
            .catch(err => {
              this.$notify.error({
                title: "删除失败",
                message: err.data.error
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleEdit(index, user) {
      this.dialogEdit = true;
      this.formData.id = user.id;
      this.formData.username = user.username;
      this.formData.email = user.email;
      this.formData.role = user.role;
    },
    submitEdit() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.editing = true;
          let data = { ...this.formData };
          if (data.password === "") {
            delete data.password;
          }
          this.editUser(data)
            .then(res => {
              this.editing = false;
              this.dialogEdit = false;
              let index = this.users.findIndex(
                item => item.id === this.formData.id
              );
              let user = this.users[index];
              user.username = this.formData.username;
              user.email = this.formData.email;
              user.role = this.formData.role;
              this.$set(this.users, index, user);
              this.$notify.success({
                title: "修改成功",
                message: "修改成功"
              });
            })
            .catch(err => {
              this.editing = false;
              this.$notify.error({
                title: "修改失败",
                message: err.data.error
              });
            });
        }
      });
    },
    handlePageChange(size) {
      this.getUsers({
        page: this.currentPage,
        pre_page: this.pageSize
      })
        .then(res => {
          this.users = res.data.data.users;
          this.totalPage = res.data.data.count;
        })
        .catch(err => {
          this.$notify.error({
            title: "获取失败",
            message: err.data.error
          });
        });
    },
    submitSearch() {
      this.$refs.searchForm.validate(valid => {
        if (valid) {
          this.searchUser(this.searchFormData)
            .then(res => {
              this.users = res.data.data;
              this.totalPage = this.users.length;
              this.pageSize = this.users.length;
            })
            .catch(err => {
              this.$notify.error({
                title: "获取失败",
                message: err.data.error
              });
            });
        }
      });
    },
    loadUsers() {
      this.searchFormData.username = "";
      this.pageSize = 10;
      this.currentPage = 1;
      this.getUsers({
        page: 1,
        pre_page: 10
      })
        .then(res => {
          this.users = res.data.data.users;
          this.totalPage = res.data.data.count;
        })
        .catch(err => {
          this.$notify.error({
            title: "获取失败",
            message: err.data.error
          });
        });
    }
  },
  mounted() {
    this.loadUsers();
  }
};
</script>

<style lang="scss" scoped>
.search-form {
  margin-top: 22px;
  margin-left: 1rem;
}

.users-table {
  margin-left: 1rem;
  margin-right: 1rem;
}

.users-pagination {
  margin: 22px 1rem;
}
</style>
